<template>
  <div>
    <h2>{{ post.title }}</h2>
    <p>{{ post.content }}</p>
    <!-- 상세 내용 표시를 위한 추가적인 내용 -->
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      post: null, // 게시글 상세 내용을 저장할 객체
    };
  },
  created() {
    // 라우트 파라미터에서 게시글 ID를 가져옴
    const postId = this.$route.params.postId;
    this.fetchPost(postId); // 게시글 상세 내용 조회
    console.log("postId: " + postId);
  },
  methods: {
    fetchPost(postId) {
      axios
        .get(`/post/${postId}`)
        .then((response) => {
          this.post = response.data;
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
};
</script>
